import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import { default as Board } from "./BackEnd/Board";
import blank from "./img/blank.png";
import fuhyou from "./img/fuhyou.png";
import tokin from "./img/tokin.png";

function NumberToImg(prop) {
  let number = prop.number;
  if (number === 0) {
    return <img className="img-fluid" src={blank} alt="0" />
  } else if (number === 1) {
    return <img className="img-fluid" src={fuhyou} alt="1" />
  } else {
    return <img className="img-fluid" src={tokin} alt="2" />
  }
}

function DisplayBoard() {

  let board = new Board();
  let boardInfo = board.boardInfo;
  // 読みにくい...
  const table = boardInfo.map((y, yKey) =>
    (
      (yKey > 0 && yKey < 10) ?
        <tr key={yKey}>
          {y.map((x, xKey) =>
            (
              (xKey > 0 && xKey < 10) ?
                <td key={xKey}>
                  <div className="btn btn-light content" id={xKey + "-" + yKey}>
                    <NumberToImg number={x} />
                  </div>
                </td> : null)
          )}
        </tr> : null
    )
  );

  return (

    <table className="table table-bordered">
      <tbody>
        {table}
      </tbody>
    </table>

  )

}


function App() {


  return (
    <div>
      <DisplayBoard></DisplayBoard>

    </div>
  );
}

export default App;
